*, *::before, *::after {
  box-sizing: border-box;
}

* {
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  min-height: 100vh;
}

img, svg, picture, video {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

h2 {
  font-size: 2rem;
  line-height: 1.2190625;
}

h3 {
  font-size: 1.125rem;
  line-height: 1.5555555556;
}

h4 {
  font-size: 4.5rem;
  line-height: 0.9861111111;
}

body {
  font-size: 0.9375rem;
  line-height: 1.8666666667;
  font-family: "Montserrat", sans-serif;
  color: hsl(232, 13%, 49%);
}

a {
  text-decoration: none;
  color: inherit;
}

body {
  background: hsl(240, 78%, 98%);
  padding-block: 4rem 4.4375rem;
}
@media (min-width: 45rem) {
  body {
    padding-block: 4.4375rem 6.4375rem;
  }
}